import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  HostListener,
  Input,
  OnInit,
  Output, QueryList,
  ViewChild,
  ViewChildren
} from '@angular/core';
import {Project} from '../model/project';
import {Power1, Circ} from 'gsap/all';

declare var TweenMax: any;
@Component({
  selector: 'app-project',
  templateUrl: './project.component.html',
  styleUrls: ['./project.component.css'],
})
export class ProjectComponent implements OnInit, AfterViewInit {
  public imageQ = 'disp';
@Input() project: Project;
  @Output() returnGallery = new EventEmitter();
  @ViewChild('containerImages') containerImages: ElementRef;
  @ViewChild('tinyLine') tinyLine: ElementRef;
  @ViewChild('bigLine') bigLine: ElementRef;
  @ViewChild('title') title: ElementRef;
  @ViewChild('description') description: ElementRef;
  @ViewChild('containerElement') containerElement: ElementRef;
@ViewChild('ifrobj') ifrobj: ElementRef;
  video = 'https://www.youtube.com/embed/CD-E-LDc384';

  private innerWidth: any;

  constructor() {
 this.qImage();
  }

  qImage() {
    this.innerWidth = window.innerWidth;
    if (this.innerWidth < 800) {
      this.imageQ = 'disp';
    } else
    if (this.innerWidth < 1600) {
      this.imageQ = 'max_1200';
    } else
    if (this.innerWidth < 2500) {
      this.imageQ = 'max_1920';
    } else {
      this.imageQ = 'max_3840';
    }

  }

  ngOnInit() {
  }
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.qImage();
  }
  closeProject() {
    TweenMax.to(this.containerElement.nativeElement, 0.25, {opacity: 0, ease: Circ.easeOut});
    this.returnGallery.emit('GALLERY');
    setTimeout(() => {this.returnGallery.emit('GALLERY'); } , 1000);
}

  ngAfterViewInit(): void {
const tm = 0.5;
    TweenMax.fromTo(this.containerImages.nativeElement, 0.5, {y: '100vh', opacity: 0}, {y: 0,  opacity: 1, ease: Circ.easeOut})
      .eventCallback('onComplete', () => {
        TweenMax.to(this.tinyLine.nativeElement, tm, {width: '33px', opacity: 1, ease: Power1.easeOut});
        TweenMax.to(this.bigLine.nativeElement, tm,  {width: '80%', opacity: 1,  ease: Power1.easeOut});
        TweenMax.fromTo(this.title.nativeElement, tm,  {transform: 'translateY(-100px)'},
          {opacity: 1, transform: 'translateY(0px)',  ease: Power1.easeOut});
        TweenMax.to(this.description.nativeElement, tm,  {opacity: 1,  ease: Power1.easeOut});
      });
  }
}



