import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Project} from '../model/project';
import {FirebaseService} from './firebase.service';
import Behance from 'behance';

@Injectable({
  providedIn: 'root'
})
export class BehanceService {
  private behance: Behance;

  constructor(private httpClient: HttpClient, private firebaseService: FirebaseService) {
    const params = {apiKey: 'u8897rZilTJ7TWx56Uw5wMfT3v7NSOCq'};
    this.behance = new Behance(params);
 // this.synchronizeWithFirebase();
  }

  synchronizeWithFirebase() {
    this.behance.userProjects('raymicfc').then(projects => {
      projects['projects'].map(singleProject => {
        this.behance.project(singleProject['id']).then(projectDetails => {
          const project = projectDetails['project'] as Project;
          const projectMaped: Project = {
            id: project.id,
            name: project.name,
            description: project.description,
            modules: [],
            covers: project.covers,
            url: project.url,
            styles: {background: {color: project.styles.background.color}}
          };
          project.modules.map(data => {
            if (data.type === 'image') {
              projectMaped.modules.push({type: data.type , text: '', embed: '', sizes: data.sizes});
            }
            if (data.type === 'embed') {
              projectMaped.modules.push({type: data.type , text: '', embed: data.embed, sizes: []});
            }
            if (data.type === 'text') {
              projectMaped.modules.push({type: data.type , text: data.text, embed: '', sizes: []});
            }
          });
          console.log(projectMaped);
          this.firebaseService.synchronizeWithBehance(projectMaped);
        });
      });
    }, error => {
      console.log(error);
    });
  }
}

