import {
  AfterContentInit, AfterViewChecked,
  AfterViewInit,
  Component, ContentChild,
  ElementRef,
  EventEmitter,
  HostListener,
  OnInit,
  Output, QueryList,
  ViewChild,
  ViewChildren
} from '@angular/core';
import {FirebaseService} from '../services/firebase.service';
import {Project} from '../model/project';
import {Power1, Circ} from 'gsap/all';
import {Globals} from '../globals';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.css']
})
export class GalleryComponent implements OnInit, AfterViewInit {
  private coverSelected: number;
  public title: string;
  public description: string;
  public projects: Array<Project>;
  @ViewChild('containerCovers') containerCovers: ElementRef;
  @Output() sendProject = new EventEmitter();
  @ViewChildren('square') squares: QueryList<any>;
  @ViewChildren('cover') cover: QueryList<any>;
  @ViewChildren('dot') dots: QueryList<any>;
  @ViewChild('cover') coverChild: ElementRef;
  @ViewChild('containerDots') containerDots: ElementRef;
  @ViewChild('bigLine') bigLine: ElementRef;
  @ViewChild('bigLineMobile') bigLineMobile: ElementRef;
  @ViewChild('titleElement') titleElement: ElementRef;
  @ViewChild('descriptionElement') descriptionElement: ElementRef;
  @ViewChild('buttonElement') buttonElement: ElementRef;
  @ViewChild('tinyLineElement') tinyLineElement: ElementRef;
  @ViewChild('containerElement') containerElement: ElementRef;
  constructor(private firebaseService: FirebaseService, private globals: Globals) {
    this.coverSelected = 0;
    this.title = 'LOADING';
    this.description = 'LOADING';
    this.firebaseService.getProjects().subscribe(data => {
      this.projects = data;
      this.projects.reverse();
      this.title = this.projects[this.coverSelected].name;
      this.description = this.projects[this.coverSelected].description;
    });
  }

  ngOnInit() {

this.coverSelected = this.globals.project;

  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    // console.log(event.target.innerWidth);
    // this.sortCovers();
  }


  left() {
    const TweenMax = this.globals.TweenMax;
    const covers = this.cover['_results'] as Array<ElementRef>;
    const beforeSelected = this.coverSelected;
    this.coverSelected--;
    let nextSelected = this.coverSelected === 0 ? covers.length - 1 : this.coverSelected - 1 ;
    if (this.coverSelected < 0) {
      this.coverSelected = covers.length - 1;
      nextSelected = covers.length - 2;
    }
    TweenMax.to(covers[this.coverSelected].nativeElement, 0.25,
      {left: 0, ease: Power1.easeOut});
    TweenMax.to(covers[beforeSelected].nativeElement, 0.25,
      {left: '-100%', ease: Power1.easeOut});
    TweenMax.to(covers[nextSelected].nativeElement, 0,
      {left: '100%'});
    this.changeProject(beforeSelected,  this.coverSelected);

  }

  right() {
    const TweenMax = this.globals.TweenMax;

    const covers = this.cover['_results'] as Array<ElementRef>;
    const beforeSelected = this.coverSelected;
    this.coverSelected++;
    let nextSelected = this.coverSelected === covers.length - 1 ? 0 : this.coverSelected + 1 ;
    if (this.coverSelected > covers.length - 1) {
      this.coverSelected = 0;
      nextSelected = 1;
    }
    TweenMax.to(covers[this.coverSelected].nativeElement, 0.25,
      {left: 0, ease: Power1.easeOut});
    TweenMax.to(covers[beforeSelected].nativeElement, 0.25,
      {left: '100%', ease: Power1.easeOut});
    TweenMax.to(covers[nextSelected].nativeElement, 0,
      {left: '-100%'});
    this.changeProject(beforeSelected,  this.coverSelected);
  }

  changeProject(beforeCover, coverSelected) {
    this.globals.project = coverSelected;
    const dots = this.dots['_results'];
    this.title = this.projects[coverSelected].name;
    this.description = this.projects[coverSelected].description;
    dots[beforeCover].nativeElement.style.opacity = 0.3;
    dots[coverSelected].nativeElement.style.opacity = 1;
  }

  goToProject() {
    const TweenMax = this.globals.TweenMax;

    TweenMax.to(this.containerElement.nativeElement, 0.5,
      {opacity: 0, ease: Power1.easeOut});
    this.sendProject.emit(this.projects[this.coverSelected]);
    setTimeout(() => {
      this.sendProject.emit(this.projects[this.coverSelected]);
    }, 1000);
  }


  ngAfterViewInit(): void {
    const TweenMax = this.globals.TweenMax;

  TweenMax.fromTo(this.containerCovers.nativeElement, 1,
    {transform: 'scale(0)', opacity: 0},
    {transform: 'scale(1)', opacity: 1, ease: Circ.easeOut})
    .eventCallback('onComplete', () => {
      this.cover['_results'][this.coverSelected].nativeElement.style.left = '0';
      this.changeProject(0, this.globals.project);
      TweenMax.fromTo(this.squares['_results'][0].nativeElement, 1,
        {y: '-50px', opacity: 0},
        {y: '0', opacity: 1, ease: Circ.easeOut});
      TweenMax.fromTo(this.squares['_results'][1].nativeElement, 1,
        {y: '50px', opacity: 0},
        {y: '0', opacity: 1, ease: Circ.easeOut});
      TweenMax.fromTo(this.containerDots.nativeElement, 1,
        {opacity: 0},
        {opacity: 1, ease: Power1.easeOut});
      TweenMax.fromTo(this.bigLineMobile.nativeElement, 0.5,
        {width: 0, marginLeft: '-40%', opacity: 0},
        {width: '90%', marginLeft: 0, opacity: 1, ease: Power1.easeOut})
      TweenMax.fromTo(this.bigLine.nativeElement, 0.5,
        {width: 0, marginLeft: '-40%', opacity: 0},
        {width: '80%', marginLeft: 0, opacity: 1, ease: Power1.easeOut})
        .eventCallback('onComplete', () => {
          TweenMax.fromTo(this.titleElement.nativeElement, 0.5,
            {x: '-50px', opacity: 0},
            {x: 0, opacity: 1, ease: Power1.easeOut});
          TweenMax.fromTo(this.tinyLineElement.nativeElement, 0.5,
            {width: '0', opacity: 0},
            {width: '30px', opacity: 1, ease: Power1.easeOut});
          TweenMax.fromTo(this.descriptionElement.nativeElement, 0.5,
            {x: '50px', opacity: 0},
            {x: 0, opacity: 1, ease: Power1.easeOut})
            .eventCallback('onComplete', () => {
              TweenMax.fromTo(this.buttonElement.nativeElement, 0.5,
                {y: '50px', opacity: 0},
                {y: 0, opacity: 1, ease: Power1.easeOut})
                .eventCallback('onComplete', () => {
                  this.changeProject(0, this.globals.project);
                });
            });
        });
    });
}

  coversFinished(project) {
 //   console.log(project);
  }



}
