import {AfterViewInit, Component, ElementRef, Input, OnInit, ViewChild, ViewChildren} from '@angular/core';
import {Power1, Circ} from 'gsap/all';
import {Globals} from '../globals';
@Component({
  selector: 'app-introduction',
  templateUrl: './introduction.component.html',
  styleUrls: ['./introduction.component.css']
})
export class IntroductionComponent implements OnInit, AfterViewInit {
  @ViewChild('photo')  photo: ElementRef;
  @ViewChild('photoMobile')  photoMobile: ElementRef;
  @ViewChild('title')  title: ElementRef;
  @ViewChild('message')  message: ElementRef;
  @ViewChild('line')  line: ElementRef;
  @ViewChildren('icon')  icons: ElementRef;
  constructor(private globals: Globals) {
  }

  ngOnInit() {

  }

  ngAfterViewInit(): void {
    const TweenMax = this.globals.TweenMax;
    TweenMax.fromTo(this.photo.nativeElement, 1, {x: 50, opacity: 0}, {x: 0,  opacity: 1, ease: Circ.easeOut});
    TweenMax.fromTo(this.photoMobile.nativeElement, 1, {x: 50, opacity: 0}, {x: 0,  opacity: 1, ease: Circ.easeOut});
    TweenMax.fromTo(this.title.nativeElement, 1, {x: -50, opacity: 0}, {x: 0,  opacity: 1, ease: Circ.easeOut})
      .eventCallback('onComplete', () => {
        TweenMax.fromTo(this.line.nativeElement, 1, {width: 0, opacity: 0, marginRight: '33px'},
          {width: '33px',  opacity: 1, marginRight: 0, ease: Circ.easeOut});
        TweenMax.fromTo(this.message.nativeElement, 1, {y: -30, opacity: 0}, {y: 0,  opacity: 1, ease: Circ.easeOut})
          .eventCallback('onComplete', () => {
            this.iconsSequence(this.icons['_results'], 0);
          });
      });

  }
  iconsSequence(icons, index) {
    const TweenMax = this.globals.TweenMax;
    if (index < icons.length) {
      TweenMax.fromTo(icons[index].nativeElement, 0.25, {y: -50, opacity: 0}, {y: 0, opacity: 1, ease: Circ.easeOut})
        .eventCallback('onComplete', () => {
          this.iconsSequence(icons, index + 1);
        });
    }
  }

}
