import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {BehanceService} from '../services/behance.service';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.css']
})
export class MenuComponent implements OnInit {
  @Input() width: string;
  @Output() valueChange = new EventEmitter();
  backupElement: any;

  constructor(behance: BehanceService) {
  // behance.synchronizeWithFirebase();
  }

  ngOnInit() {
  }

  handleclick(value, context) {
    this.lineAnimation(context);
    this.valueChange.emit(value);
  }

  lineAnimation(line) {
    line.className += ' active';
    if (this.backupElement) {
      if (this.backupElement !== line) {
        this.backupElement.className = 'line ';
        this.backupElement = line;
      }
    } else {
      this.backupElement = line;
    }
  }

}
