import {AfterViewChecked, AfterViewInit, Component, ElementRef, OnInit, QueryList, ViewChild} from '@angular/core';
import {Power1, Circ} from 'gsap/all';
import {Globals} from '../globals';
@Component({
  selector: 'app-more',
  templateUrl: './more.component.html',
  styleUrls: ['./more.component.css']
})
export class MoreComponent implements OnInit, AfterViewInit {
@ViewChild('h3Child') h3Child: ElementRef;
@ViewChild('lineOneChild') lineOneChild: ElementRef;
@ViewChild('h1Child') h1Child: ElementRef;
@ViewChild('lineTwoChild') lineTwoChild: ElementRef;
@ViewChild('pOneChild') pOneChild: ElementRef;
@ViewChild('lineThreeChild') lineThreeChild: ElementRef;
@ViewChild('iconsChildren') iconsChildren: ElementRef;
@ViewChild('namesChildren') namesChildren: ElementRef;
@ViewChild('footer') footer: ElementRef;

constructor(private globals: Globals) { }

  ngOnInit() {
  }

  ngAfterViewInit(): void {
  const TweenMax = this.globals.TweenMax;
    TweenMax.fromTo(this.h3Child.nativeElement, 0.25,
      {y: '-50px', opacity: 0},
      {y: 0, opacity: 1})
      .eventCallback('onComplete', () => {
        TweenMax.fromTo(this.lineOneChild.nativeElement, 0.25,
          {y: '-50px', opacity: 0},
          {y: '0', opacity: 1})
          .eventCallback('onComplete', () => {
            TweenMax.fromTo(this.h1Child.nativeElement, 0.25,
              {y: '-50px', opacity: 0},
              {y: '0', opacity: 1})
              .eventCallback('onComplete', () => {
                TweenMax.fromTo(this.lineTwoChild.nativeElement, 0.25,
                  {y: '-50px', opacity: 0},
                  {y: '0', opacity: 1})
                  .eventCallback('onComplete', () => {
                    TweenMax.fromTo(this.pOneChild.nativeElement, 0.25,
                      {y: '-50px', opacity: 0},
                      {y: '0', opacity: 1})
                      .eventCallback('onComplete', () => {
                        TweenMax.fromTo(this.lineThreeChild.nativeElement, 0.25,
                          {y: '-50px', opacity: 0},
                          {y: '0', opacity: 1})
                          .eventCallback('onComplete', () => {
                            TweenMax.fromTo(this.iconsChildren.nativeElement, 0.25,
                              {y: '50px', opacity: 0},
                              {y: '0', opacity: 1})
                              .eventCallback('onComplete', () => {
                                TweenMax.fromTo(this.namesChildren.nativeElement, 0.25,
                                  {y: '50px', opacity: 0},
                                  {y: '0', opacity: 1})
                                  .eventCallback('onComplete', () => {
                                    TweenMax.fromTo(this.footer.nativeElement, 0.25,
                                      {y: '50px', opacity: 0},
                                      {y: '0', opacity: 1})
                                      .eventCallback('onComplete', () => {

                                      });
                                  });
                              });
                          });
                      });
                  });
              });
          });
      });
  }


}
