import {AfterViewInit, Component, ElementRef, ViewChild} from '@angular/core';
import {Project} from './model/project';
import {Power1, Bounce, Circ, Power0} from 'gsap/all';
import {Globals} from './globals';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
isIntroduction: boolean;
isGallery: boolean;
isMore: boolean;
isProject: boolean;
  project: Project;
constructor(private globals: Globals) {
  this.isIntroduction = this.showComponent();
}
  menuSelected(value) {
    switch (value) {
      case 'RAYMI': {
        this.isIntroduction =  this.showComponent();
        break;
      }
      case 'GALLERY': {
        this.isGallery = this.showComponent();
        break;
      }
      case 'MORE': {
        this.isMore = this.showComponent();
        break;
      }
    }
  }
  // *ngIf="isProject"
  openProject(project: Project) {
const TweenMax = this.globals.TweenMax;
    this.isProject = this.showComponent();
    const menu = document.querySelectorAll('app-menu');
    const widthMenu = 156 + 1;
    const heightMenu = 80 + 1;
    TweenMax.to(menu, 0.5,  {marginLeft: -widthMenu + 'px', marginTop: -heightMenu + 'px'});
  }
  closeProject(value) {
    const TweenMax = this.globals.TweenMax;
    const menu = document.querySelectorAll('app-menu');
    TweenMax.to(menu, 0.5,  {marginLeft: 0, marginTop: 0})
      .eventCallback('onComplete', () => {
        this.menuSelected(value);
      });
  }

  showComponent(): boolean {
    this.isIntroduction = false;
    this.isGallery = false;
    this.isMore = false;
    this.isProject = false;
    return true;
  }


}
